export default {
  data() {
    return {
      scrollItem: ['登记信息', '办理跟踪'],
      genderOptions: [['男', '女']],
      documentTypeOptions: [['身份证', '军官证', '其他']],
      providerTypeOptions: [['企业', '个人']],
      ageGroupOptions: [['少年(19岁一下)', '中青年(19-65岁)', '老年人']],
      identityOptions: [['城镇', '乡村']],
      marketSubjectTypeOptions: [['marketSubjectTypeOptions']],
      industryClassificationOptions: [['自然人']],
      objectClassOptions: [['商品', '服务']],
      sellTypeOptions: [['网购', '实体店']],
      stepOptions: ['保存', '受理', '分派下级', '分派部门', '不受理', '退回'],
      questionTypeOptions: [],
      nowIndex: 0,
      title: '',
      contactNumber: '',
      mailBox: '',
      idNum: '',
      unitOfComplainant: '',
      consumptionAmount: '',
      complainAddress: '',
      zipCode: '',
      nation: '',
      electronicPlatform: '',
      OrderNumber: '',
      otherContactInfo: '',
      addressOfComplainant: '',
      nationality: '',
      contacts: '',
      timeOfDispute: '',
      brand: '',
      complainedObject: '',
      phoneNumber: '',
      productName: '',
      productBatchNumber: '',
      productSpecification: '',
      productValidity: '',
      salesEnterprise: '',
      keyWord: '',
      approvalNumber: '',
      contentOfAppeal: '',
      socialCreditCode: '',
      reportContent: '',
      Manufacturer: '',
      flowList: [1, 2, 3, 4, 5, 6, 7, 8, 9],
      gender: [],
      ageGroup: [],
      marketSubjectType: [],
      objectClass: [],
      sellType: [],
      questionType: [],
      identity: [],
      providerType: [],
      documentType: [],
      industryClassification: [],
      show: false,
      stepValue: '保存',
      // 举报涉及客体信息
      serviceName: '',
      typesOfReportedProblemsOptions: [['其他']],
      typesOfReportedProblems: [],
    };
  },
  methods: {
    toggleTabs(index) {
      this.nowIndex = index;
    },
    change() {},
    onConfirm() {},
    showChoose() {
      this.show = true;
    },
    comfirm() {
      if (this.stepValue === '保存') {
        const params = {
          stepValue: this.stepValue,
        };
        this.$router.push({
          path: '/main/DetailOf12345/accept12345form',
          query: params,
        });
      } else if (this.stepValue === '受理') {
        const params = {
          stepValue: this.stepValue,
        };
        this.$router.push({
          path: '/main/DetailOf12345/accept12345form',
          query: params,
        });
      } else if (this.stepValue === '分派下级') {
        const params = {
          stepValue: this.stepValue,
        };
        this.$router.push({
          path: '/main/DetailOf12345/accept12345form',
          query: params,
        });
      } else if (this.stepValue === '分派部门') {
        const params = {
          stepValue: this.stepValue,
        };
        this.$router.push({
          path: '/main/DetailOf12345/accept12345form',
          query: params,
        });
      } else if (this.stepValue === '不受理') {
        const params = {
          stepValue: this.stepValue,
        };
        this.$router.push({
          path: '/main/DetailOf12345/accept12345form',
          query: params,
        });
      } else {
        console.log(this.stepValue);
      }
    },
  },
};



