export default {
  data() {
    return {
      subjectInvolved: '',
      businessAddress: '',
      detailAddress: '',
      administrativeDivision: '',
      businessName: '',
      documentNo: '',
      managementDepartment: '',
      waysOfCompensation: '',
      placeOfSale: '',
      mentalCompensationAmount: '',
