      postscript: '',
      typeOfInfringement: [],
      region: [],
      subjectTyped: [],
      evidence: [],
      fraudSign: [],
      resultsOfConciliation: [],
      transferTheCase: [],
      unlicensedOperation: [],
      mediationMode: [],
      unfulfilledObligations: [],
